*{
  padding: 0;
  margin:0;
  box-sizing: border-box;
}
:root{
  
  --nav: #f4f4f4 ;
  --back:#f4f4f4;
  --font:#01247a;
  --height:100vh;
  --border: #a9b2b7;
}
body{
  flex-direction: column;
  margin:auto;
  align-items: center;
  justify-content: center;
  background-color: var(--back);
  height:var(--height);
  font-family: 'Lato', sans-serif;
}

.nav{
  width:100%;
  position: fixed;
  display: flex;
  top: 0;
  left:0;
  background: var(--nav);
  backdrop-filter:blur(20px);
  align-items: center;
  justify-content:flex-end;
  z-index: 5;
  font-family: "Source Sans Pro Topnav", sans-serif;
  margin-bottom: 20px;
}

.nav .Link,
.nav .login{
  text-decoration: none;
  color:var(--font);
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  margin: 20px;
  margin-right: 3em;
  position: relative;
  float:right;
  font-size: 23px;
  font-weight: 500;
}
.nav .login{
  background-color:#01247a;
  color: #fefefe;
  border:#01247a 1px;
  border-radius: 10px;
  padding:10px 20px;
  margin:5px 20px 5px 0;
}
.nav .login:hover{
  background-color: #033ccd;
}
.nav .Link::after{
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: var(--font);
  border-radius: 3px;
  transition: .5s ease-in-out;
  transform:scalex(0);
  
}
@media (max-width: 800px) {
 .nav {
   flex-direction: column;
 }
 .dropdown-content{
     left:90px;
 }
}

.nav .Link:hover:after, .dropdown:hover #contact:after{
  transform: scaleX(1);
}

a.active .Link{
  color: #033ccd;
}

a.active .login{
  background-color:#033ccd;
}


a.active .Link::after{
  transform: scaleX(1);
  background-color:#033ccd;
  height: 4px;
}

