select {
  /* General styling */
  appearance: none; /* Remove default styling */
  background-color: #fff; /* White background */
  border: 1px solid #ccc; /* Gray border */
  border-radius: 5px; /* Rounded corners */
  padding: 10px 15px; /* Padding for text */
  font-size: 14px; /* Font size */
  width: 200px; /* Adjust width as needed */
  cursor: pointer; /* Hand cursor on hover */
  margin-right: 10px;

  /* Hover state */
  &:hover {
    border-color: #999; /* Darker border on hover */
  }

  /* Arrow icon for dropdown */
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='%23333'%3E%3Cpath d='M7 10l5 5 5-5z'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: right 10px center;
  background-size: 12px;
}

/* Option styling */
option {
  background-color: #fff; /* White background for options */
  color: #333; /* Black text color */
  padding: 10px; /* Padding for options */
}

.get-courses input[type="checkbox"] {
  /* General styling */
  
  width: 20px; /* Adjust size as needed */
  height: 20px;
  border: 2px solid #ccc; /* Gray border */
  border-radius: 5px; /* Rounded corners */
  cursor: pointer; /* Hand cursor on hover */
  margin-left: 15px;
}

.get-courses label{
  padding: 10px;
}